.label
    display: inline-block
    line-height: 1.5em
    border: 0px
    font-size: 1.05em
    font-family: inherit
    max-width: 100%
    margin: 0.5em

.solid_box, .liquid_box
    border-radius: 3px
    margin: 0.5em

.solid_box
    border: 1px solid #CCC
    padding: 0.7em
    min-width: calc(100% - 1em)
    display: flex
    flex-wrap: wrap
    align-content: space-between
    align-items: flex-start

    .label
        background: #545BE2
        padding: 0.1em 0.5em 0.1em 0.5em
        color: white
        border-radius: 3px

.liquid_box
    padding: 0.5em
    background: #F0F0F0
    border: 1px solid #F0F0F0
    white-space: pre-wrap
    vertical-align: top
    font-size: 1em

    .label
        background: #8589e5
        margin: 0em 0.5em 0em 0em
        padding: 0em 0.4em 0em 0.4em
        border-radius: 3px
        vertical-align: top
        color: white
